import React, { Fragment, useEffect, useState } from 'react';
import { Row, Card, CardBody, Col } from "reactstrap";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from "../../../containers/navs/Breadcrumb";
import mUser from '../../../Controler/c_User'
import Skeleton from 'react-loading-skeleton';
import { TextField } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Notifikasi from '../../../helpers/Notifikasi'
import { isArray } from "util";


const Riwayat = (props) => {
    const authLogin = JSON.parse(localStorage.getItem('keminaAdmin'))
    const [user, setUser] = useState({})
    const [isLoad, setIsLoad] = useState(true)
    useEffect(() => {
        mUser.detailUser(authLogin.clientID, authLogin.token, authLogin.username, authLogin.jenisUser).then(resp => {
            setUser(resp);
        }).catch(err => {
            console.error(err);
        }).finally(() => setIsLoad(false))
        return () => {

        }
    }, [isLoad, authLogin.clientID, authLogin.token, authLogin.username, authLogin.jenisUser])

    const loadingRow = [0, 1, 2]
    const handleChange = (ev) => {
        var value = ev.target.value;
        const key = ev.target.id || ev.target.name;
        var newForm = { ...user }
        newForm[key] = value;
        setUser(newForm);
    }
    const handleUpdate = () => {
        const _alamat = user.alamat.replace(/\n/g, "<BR>");
        mUser.updateProfil(authLogin.clientID, authLogin.token, authLogin.username, user.nama, user.noHp, _alamat, authLogin.jenisUser, authLogin.klinik.kodeklinik, authLogin.klinik.namaklinik).then(resp => {
            if (resp) {
                Notifikasi("success", "Profil berhasil di update", "")
                setIsLoad(true)
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })
    }
    return (
        <Fragment>
            <Row>
                <Colxx xxs="12">
                    <Breadcrumb heading="menu.profil" match={props.match} />
                    <Separator className="mb-5" />
                </Colxx>
            </Row>

            <Row>
                {isLoad ? (
                    <Col>
                        {loadingRow.map(el => (
                            <Card key={el} style={{ marginTop: 5 }}>
                                <CardBody>
                                    <Skeleton height={100} />
                                </CardBody>
                            </Card>
                        ))}
                    </Col>
                ) : (
                        <Col>
                            <Card style={{ marginTop: 5 }}>
                                <CardBody>
                                    <Row>
                                        <Col xs={12}>
                                            <TextField
                                                id="username"
                                                name="username"
                                                label="E-mail"
                                                fullWidth
                                                value={user.username}
                                                disabled
                                                variant="outlined"
                                                type="mail"
                                            />
                                        </Col>
                                        <Col xs={12} md={12} className="marginFrom">
                                            <TextField
                                                id="nama"
                                                name="nama"
                                                label="Nama Lengkap"
                                                fullWidth
                                                value={user.nama}
                                                onChange={handleChange}
                                                variant="outlined"
                                            />
                                        </Col>
                                        <Col xs={12} md={12} className="marginFrom">
                                            <TextField
                                                id="noHp"
                                                name="noHp"
                                                label="HP"
                                                fullWidth
                                                value={user.noHp}
                                                onChange={handleChange}
                                                variant="outlined"
                                            />
                                        </Col>
                                        <Col xs={12} md={12} className="marginFrom">
                                            <TextField
                                                id="alamat"
                                                name="alamat"
                                                label="Alamat"
                                                fullWidth
                                                value={user.alamat}
                                                onChange={handleChange}
                                                variant="outlined"
                                                multiline
                                                rows="3"
                                            />
                                        </Col>
                                        <Col xs={12} className="marginFrom">
                                            <Button variant="contained" fullWidth color="primary" onClick={handleUpdate}>Update Profil</Button>
                                        </Col>
                                    </Row>
                                </CardBody>
                            </Card>
                        </Col>
                    )}
            </Row>
        </Fragment>
    )
};
export default Riwayat;
